@use 'var' as v; 

@mixin tel {
     @media( min-width: 480px)
     {
        @content;
     }
}
@mixin tab {
     @media(min-width:780px )
     {
        @content; 
     }
}
@mixin des {
   @media(min-width:1080px )
   {
      @content; 
   }
}

@mixin grid($var, $var2){
   padding: 5rem 0;
   display: grid;
   grid-template-columns: repeat($var, 1fr);
   gap: $var2;
}

@mixin reset-ls{
   margin: 0;
   padding: 0;
   list-style: none;
   
}