@use '../Base/var' as v; 
@use '../Base/mixin' as m; 


.video{
position:relative;
max-height: 70rem;
min-width: 50rem;
overflow: hidden;
.overlay{
     position: absolute;
     background: rgb(231,205,0);
     background: linear-gradient(353deg, rgba(231,205,0,0) 0%, rgba(11,6,113,1) 95%);
     height: 100%;
     width: 100%;
     display: flex;
     flex-direction: column;
     justify-content: center;
     }
video{
    width: auto;
    display: block;
    @include m.tab {
        width: 100%;
    }
}
.cont-vid{
    z-index: 1;
    h2{
        color: v.$color-4;
        font-size: 7rem;
        margin: 0;
    }
    samp{
        color: v.$color-5;
        font-size: 6rem;
        margin: 0;
    }
    p{
        background-color: v.$color-3;
        display: inline-block;
        padding:  1rem 2rem ;
        color: v.$bn;
        border-radius: 0.5rem;
        font-weight: bold;
        margin: auto;
    }
}
}