.parte-2 {
  color: #1ec008;
}

.header {
  background-color: #000000;
}
@media (min-width: 780px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem #333;
  }
}
.header h1 {
  color: #FFFF33;
  text-align: center;
  margin: 1rem;
}
.header .cont-header {
  padding: 2rem;
  align-items: center;
}
@media (min-width: 780px) {
  .header .cont-header {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: 780px) {
  .nav-prin {
    display: flex;
    gap: 2rem;
  }
}
.nav-prin a {
  display: block;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 780px) {
  .nav-prin a {
    margin: 0;
  }
}
.nav-prin a:hover {
  color: #FF0099;
}

.video {
  position: relative;
  max-height: 70rem;
  min-width: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(231, 205, 0);
  background: linear-gradient(353deg, rgba(231, 205, 0, 0) 0%, rgb(11, 6, 113) 95%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 780px) {
  .video video {
    width: 100%;
  }
}
.video .cont-vid {
  z-index: 1;
}
.video .cont-vid h2 {
  color: #1ec008;
  font-size: 7rem;
  margin: 0;
}
.video .cont-vid samp {
  color: #FFFF33;
  font-size: 6rem;
  margin: 0;
}
.video .cont-vid p {
  background-color: #FF0099;
  display: inline-block;
  padding: 1rem 2rem;
  color: #FFFFFF;
  border-radius: 0.5rem;
  font-weight: bold;
  margin: auto;
}

.sb-fest {
  margin-top: 2rem;
}
@media (min-width: 780px) {
  .sb-fest {
    padding: 5rem 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.cont-fest {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 780px) {
  .cont-fest {
    margin-top: 0;
  }
}
.cont-fest h2 {
  margin-bottom: 2rem;
  font-weight: bold;
}
.cont-fest .fexha {
  color: #1ec008;
  font-weight: bold;
}

.lineup {
  background-color: #0033FF;
  padding: 5rem 0;
}

h3, p {
  text-align: center;
}

.dia {
  text-transform: uppercase;
  margin: 5rem;
  color: #FFFFFF;
}

@media (min-width: 780px) {
  .esc-cont {
    padding: 5rem 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.esc-cont p {
  color: #000000;
}
.esc-cont .esc {
  padding-top: 2rem;
}
.esc-cont .esc p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.pop .nm-esc:after {
  background-image: url(../img/icono_edm.png);
}

.rk .nm-esc:after {
  background-image: url(../img/icono_rock.png);
}

.bg-amar {
  background-color: #FFFF33;
}
.bg-amar li:nth-child(odd) {
  background-color: #cccc00;
}

.bg-ver {
  background-color: #1ec008;
}
.bg-ver li:nth-child(odd) {
  background-color: #0f5e04;
}

.cal {
  margin: 0;
  padding: 0;
  list-style: none;
}
.cal li {
  padding: 5rem;
  font-weight: 700;
}
.cal li span {
  font-weight: 400;
}

.gal {
  padding: 5rem 0;
}
.gal h3 {
  text-align: center;
}
.gal .gal-img {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 5rem 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .gal .gal-img {
    padding: 5rem 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 780px) {
  .gal .gal-img {
    padding: 5rem 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1080px) {
  .gal .gal-img {
    padding: 5rem 0;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}
.overlay .btn-cerrar {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.overlay .btn-cerrar:hover {
  cursor: pointer;
}

.blt {
  padding: 5rem 0;
}
.blt h3 {
  text-align: center;
  color: #FFFFFF;
}

@media (min-width: 780px) {
  .cont-blt {
    padding: 5rem 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass .nm-pass {
  text-align: center;
  font-size: 2.4rem;
  color: #FFFFFF;
}
.pass-header p, .pass-footer p {
  text-align: center;
  color: #FFFFFF;
  margin: 0%;
}
.pass-body {
  background-color: #FFFFFF;
}
.pass-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass-body ul li,
.pass-body ul :nth-child(odd) {
  background-color: #FFFFFF;
  padding: 1rem;
  border-bottom: 1px solid #000000;
}
.pass-header {
  padding: 1rem;
  background-color: #FF0099;
}
.pass-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pass-footer {
  background-color: #0033FF;
}
.pass-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.footer {
  background-color: #0033FF;
  padding: 1rem;
}
.footer p {
  margin: 0;
  text-align: center;
  color: #FFFFFF;
}