@use '../Base/var' as v ; 
@use '../Base/mixin' as m ; 

.sb-fest{
    margin-top: 2rem;
  @include m.tab {
    @include m.grid(2, 5rem);
  }
}
.cont-fest{
    display:  flex; 
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    @include m.tab {
        margin-top:0;
    }
    h2{
        margin-bottom: 2rem;
        font-weight: bold;
    }
    .fexha{
        color: v.$color-4;
        font-weight: bold;
    }
}