@use '../Base/var' as v ; 
@use '../Base/mixin' as m ; 

.blt    {
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$bn;
    }
}
.cont-blt{
    @include m.tab
    {
        @include m.grid(2,5rem)
    }
}
.pass{

    .nm-pass{
        text-align: center;
        font-size: 2.4rem;
        color: v.$bn;
    }
    &-header,&-footer{
        p{
            text-align: center;
            color: v.$bn;
            margin: 0%;
        }
    }
    &-body{
        background-color: v.$bn;
        ul{
            @include m.reset-ls;
            li,
            :nth-child(odd){
                background-color: v.$bn;
                padding: 1rem;
                border-bottom: 1px solid v.$ng;
            }
        }
    }    
    &-header{
        padding: 1rem;
        background-color: v.$color-3;
        p{
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }
    &-footer{
        background-color: v.$color-2;
       p{
            font-size: 2.4rem;
            font-weight: 700;
       }
    }    
}