
@use '../Base/var' as v;
@use '../Base/mixin' as m;

.parte-2{
     color: v.$color-4;
}
.header {
background-color: v.$ng ;
@include m.tab {
    &.fijo{
        left: 0;
        top: 0 ;
        position: fixed;
        width: 100%;
        box-shadow: .3rem .3rem .3rem #333;
    }
}
    h1 {
        color: v.$color-5;
        text-align: center;
        @include m.tab {
         
            
        }
        margin: 1rem;
    }
    .cont-header{
        padding: 2rem; 
        align-items: center ;
         @include m.tab {
            display: flex;
            justify-content: space-between;
         }
    }
    
}
.nav-prin{
    @include m.tab {
        display: flex;
        gap: 2rem;
    }
    a{
        display: block;
        color: v.$bn;
        @include m.tab {
            margin: 0; 
        }
        text-align: center;

        &:hover{
            color: v.$color-3;
        }
    }
}