@use '../Base/var' as v ;
@use '../Base/mixin' as m ;  

.lineup{
    background-color: v.$color-2;
    padding: 5rem 0 ;
}
h3,p{
    text-align: center ;
  
}
.dia {
  text-transform: uppercase;  
  margin: 5rem;
  color: v.$bn;
}
.esc-cont{
    @include m.tab {
        @include m.grid(2, 5rem)
    }
    p{
        color: v.$ng;
    }
    .esc{
        padding-top: 2rem;
        p{
            &:after{
                content: '';
                display: block;
                width: 20rem;
                height: 10rem;
                background-size: 15rem;
                background-repeat: no-repeat;
               background-position: center center;
               margin: 2rem auto 0 auto;
            }
        }
    }
}
.pop .nm-esc:after{
    background-image: url(../img/icono_edm.png);
}
.rk .nm-esc:after{
    background-image: url(../img/icono_rock.png);
}
.bg-amar{
    background-color: v.$color-5;
    li:nth-child(odd)
    {
        background-color: darken(v.$color-5, 20%);
    }
}
.bg-ver{
    background-color: v.$color-4;
    li:nth-child(odd)
    {
        background-color: darken(v.$color-4, 20%);
    }
}
.cal{
  @include m.reset-ls;
    li{
        padding: 5rem;
        font-weight: 700;
        span{
            font-weight: 400;
        }
    }
}