@use '../Base/var' as v; 
@use '../Base/mixin' as m;

.gal{
    padding: 5rem 0 ;
    h3{
         text-align: center;

    }
    .gal-img   {
        @include m.reset-ls;
        @include  m.grid(2 ,2rem);
        @include m.tel{
            @include m.grid(3 , 2rem)
            
        }
        @include m.tab {
            @include m.grid(4,2rem)
            
        }
        @include m.des {
            @include m.grid(6 , 2rem)
        }
    }
}
.overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display : grid;
    place-items: center;

    background-color: rgb(0 0 0 /.5);

    picture{
        display: block;
        max-width: 80rem;
    }
    .btn-cerrar{
         width: 5rem;
         height: 5rem;
         border-radius: 50%;
         background-color: v.$bn;
         color: v.$ng;
         display: grid;
         place-items: center;
         font-weight: 700;

         &:hover {
            cursor: pointer;
         }

    }
}